const enter='animation-slide-enter';
const out='animation-slide-out';

const danger='#e91801';
const info='#00a1e9';
const success='#00bb0c';

export default{
	props:{
		duration:{
			type:Number,
			default:1500,
		},
	},
	data(){
		return{
			animation:'',
			realShow:false,
			backgroundColor:'',
			content:''
		}
	},
	methods:{
		show(content,color){
			if(this.animation===enter)
				return;
			this.content=content;
			this.backgroundColor=this.COLORCONFIG.info;
			if(color)
				this.backgroundColor=color;
			this.realShow=true;
			this.animation=enter;
			setTimeout(()=>{
				this.hide();
			},this.duration)
		},
		hide(){
			if(!this.animation===enter)
				return;
			this.animation=out;
			// this.realShow=false;
		}
	}
}
